<template>
  <md-app>
    <md-app-toolbar class="md-primary">
      <h1>時刻表</h1>
    </md-app-toolbar>
    <md-app-content>
      <div class="md-subhead">出発時刻</div>
      <VueCtkDateTimePicker
        v-model="requestDate"
        :minute-interval="1"
        :format="'YYYY-MM-DD HH:mm'"
        :overlay="true"
        locale="ja"
        label
      ></VueCtkDateTimePicker>
      <md-table>
        <md-table-toolbar>
          <h2 class="md-title">バス時刻表</h2>
        </md-table-toolbar>
        <md-table-row>
          <md-table-head v-for="head in busHeaders" v-bind:key="head">{{head}}</md-table-head>
        </md-table-row>
        <md-table-row v-for="col in busRecords" v-bind:key="col.id">
          <md-table-cell>{{col.time}}</md-table-cell>
        </md-table-row>
      </md-table>
      <md-table>
        <md-table-toolbar>
          <h2 class="md-titile">電車時刻表</h2>
        </md-table-toolbar>
        <md-table-row>
          <md-table-head v-for="head in trainHeaders" v-bind:key="head">{{head}}</md-table-head>
        </md-table-row>
        <md-table-row v-for="col in trainRecords" v-bind:key="col.id">
          <md-table-cell>{{col.trainType}}</md-table-cell>
          <md-table-cell>{{col.destinationName}}</md-table-cell>
          <md-table-cell>{{col.time}}</md-table-cell>
        </md-table-row>
      </md-table>
    </md-app-content>
  </md-app>
</template>

<script>
import {
  getShirayuriBusStop,
  getKeikyuTrain
} from "./components/time-table-model.js";
import moment from "moment";

export default {
  components: {},
  data() {
    return {
      // datetimepickerはStringで受け渡すためDateは不可
      requestDate: moment()
        .format("YYYY-MM-DD HH:mm")
        .toString(),
      // バス時刻ヘッダ
      busHeaders: ["発車時刻"],
      // バス時刻表
      busRecords: getShirayuriBusStop(moment(this.requestDate)),
      // 電車時刻表ヘッダ
      trainHeaders: ["種別", "行き先", "発車時刻"],
      // 電車時刻表
      trainRecords: getKeikyuTrain(moment(this.requestDate))
    };
  },
  watch: {
    requestDate: function(val) {
      this.busRecords = getShirayuriBusStop(moment(val));
      this.trainRecords = getKeikyuTrain(moment(val));
    }
  }
};
</script>